export type language = {
    id: string,
    name: string,
    path: string,
    contents: string,
}

export function getLanguage(path: string, languages: Array<language>): language {
    let defaultLanguage: language;
    for (const language of languages) {
        if (  !language.id || !language.path && (language.path !== '')  ) throw Error('Language configuration error, please add "id" and "path" in graphql')

        if (language.id === 'en') defaultLanguage = language;
        else if (path.match(new RegExp(`\/${language.path}\/?$`))) return language;
    }
    return defaultLanguage!;
}