import React, { FC } from 'react';
import { Link, useStaticQuery, graphql, navigate } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';

import { language, getLanguage } from '../../tools/Language';
import * as style from './index.module.scss';


type QueriedData = {
    site: {
        siteMetadata: {
            languages: Array<language>
        }
    }
}

type iProps = {
    currentPath: string
}
const Navigation: FC<iProps> = ({ currentPath }) => {
    const { site }: QueriedData = useStaticQuery(graphql`
        query Navigation {
            site {
                siteMetadata {
                    languages {
                        id
                        name
                        path
                    }
                }
            }
        }
    `)
    const languages = site.siteMetadata.languages;

    const navigateTo = (fromLanguagePath: string, toLanguagePath: string) => {
        navigate( currentPath.replace(new RegExp(`\/${fromLanguagePath}\/?$`), `\/${toLanguagePath}`) );
    }


    return (
        <nav
            className={style.nav}
        >
            <Link
                to='/'
                className={style.link}
            >
                <StaticImage
                    className={style.img}
                    src='../../images/blog.png'
                    alt='blog logo'
                    title='SNinjo Blog'
                    loading='eager'
                />
            </Link>
            <a
                href='https://SNinjo.com'
                target='_blank'
                className={style.link}
            >
                <StaticImage
                    className={style.img}
                    src='../../images/sninjo.png'
                    alt='SNinjo logo'
                    title='SNinjo Personal Website'
                    loading='eager'
                />
            </a>
            <a
                href='https://SNinjo.com/contact'
                target='_blank'
                className={style.link}
            >
                <StaticImage
                    className={style.img}
                    src='../../images/email.png'
                    alt='contact icon'
                    title='Contact Information'
                    loading='eager'
                />
            </a>

            <select
                defaultValue={getLanguage(currentPath, languages).path}

                onChange={  event => navigateTo(getLanguage(currentPath, languages).path, event.target.value)  }
            >
                {
                    languages.map((language, index) => (
                        <option
                            key={`Language${index}`}
                            value={language.path}
                        >
                            {language.name}
                        </option>
                    ))
                }
            </select>
        </nav>
    )
}
export default Navigation;